import Link from 'next/link';
import fetch from 'isomorphic-unfetch';
import MasterPage from '../components/Master';
import Precio from '../components/Precio';
import Noticias from '../components/Noticias';

const Index = (props) => (
	
	<MasterPage>
		<div className="row">
			<div className="col-12">
				<h5>Precio del bitcoin</h5>
				<Precio
				precio={props.precioBitcoin}
				/>
			</div>

			<div className="col-md-8">
				<h5>Noticias sobre bitcoin</h5>
				<Noticias
				noticias={props.noticias}
				/>
			</div>
			<div className="col-md-4">
				<h5>Proximos eventos bitcoin</h5>
				<Eventos
				eventos={props.eventos}
				/>
			</div>
		</div>
	</MasterPage>

	);

Index.getInitialProps = async () =>{
	const precio = await fetch(`https://api.coinmarketcap.com/v2/ticker/1/`);
	const noticias = await fetch(`https://newsapi.org/v2/everything?q=bitcoin&sortBy=publishedAt&apiKey=27c901ec4ce147ef9174dd99efbced5c&language=es`);
	const eventos = await fetch(`https://www.eventbriteapi.com/v3/events/search/?q=Bitcoin&sort_by=date&location.address=Colombia&token=RN6INPWFCPRULWPMOKJ3`);

	const resPrecio = await precio.json();
	const resNoticias = await noticias.json();
	const resEventos = await eventos.json();

	return{
		precioBitcoin: resPrecio.data.quotes.USD,
		noticias: resNoticias.articles,
		eventos: resEventos

	}
}


export default Index